"use client";

import React from 'react';
import { Leaf, Recycle, Package } from 'lucide-react';

const WelcomeScreen = ({ onStartScan, onEnvironmentReport }) => {
  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 via-white to-green-100 flex flex-col">
      {/* 상단 로고/아이콘 영역 */}
      <div className="flex-1 flex flex-col justify-center items-center px-6 pt-20">
        {/* 메인 아이콘 */}
        <div className="relative mb-12">
          {/* 배경 원형 효과 */}
          <div className="absolute inset-0 w-32 h-32 bg-green-800/10 rounded-full animate-pulse"></div>
          <div className="absolute inset-2 w-28 h-28 bg-green-800/20 rounded-full animate-pulse" style={{animationDelay: '0.5s'}}></div>
          
          {/* 중앙 아이콘 */}
          <div className="relative w-32 h-32 bg-green-800 rounded-full flex items-center justify-center shadow-2xl">
            <div className="flex items-center gap-1">
              <Package className="w-8 h-8 text-white" />
              <Leaf className="w-6 h-6 text-green-200" />
            </div>
          </div>
        </div>

        {/* 인사말 */}
        <div className="text-center mb-16">
          <h1 className="text-4xl font-bold text-green-800 mb-6 leading-tight">
            안녕하세요
          </h1>
          <div className="space-y-3">
            <p className="text-2xl text-green-700 font-medium">
              환경을 생각하는 포장,
            </p>
            <p className="text-3xl font-bold text-green-800">
              PACO입니다.
            </p>
          </div>
        </div>

        {/* 환경보호 아이콘들 */}
        <div className="flex justify-center gap-8 mb-16">
          <div className="flex flex-col items-center">
            <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mb-2">
              <Recycle className="w-6 h-6 text-green-700" />
            </div>
            <span className="text-sm text-green-600">재활용</span>
          </div>
          <div className="flex flex-col items-center">
            <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mb-2">
              <Leaf className="w-6 h-6 text-green-700" />
            </div>
            <span className="text-sm text-green-600">친환경</span>
          </div>
          <div className="flex flex-col items-center">
            <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mb-2">
              <Package className="w-6 h-6 text-green-700" />
            </div>
            <span className="text-sm text-green-600">최적화</span>
          </div>
        </div>
      </div>

      {/* 하단 버튼 영역 */}
      <div className="px-6 pb-12 space-y-4">
        {/* 메인 버튼 */}
        <button
          onClick={onStartScan}
          className="w-full bg-green-800 text-white py-5 rounded-full text-xl font-bold hover:bg-green-900 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-[1.02]"
        >
          상품 스캔하기
        </button>

        {/* 서브 버튼 */}
        <button
          onClick={onEnvironmentReport}
          className="w-full border-2 border-green-300 text-green-800 py-4 rounded-full text-lg font-semibold hover:bg-green-50 transition-all duration-300"
        >
          환경 리포트 확인하기
        </button>
      </div>

      {/* 하단 작은 텍스트 */}
      <div className="text-center pb-8">
        <p className="text-sm text-green-600/80">
          지구를 생각하는 스마트 포장 솔루션
        </p>
      </div>
    </div>
  );
};

export default WelcomeScreen;